<template>
  <section>
    <v-header></v-header>
      <div class="code-container">
        <v-container></v-container>
      </div>
  </section>
</template>

<script>
  import Header from '../header/header.vue'
  import Container from '../container/container.vue'
  import { EventBus } from '../../services/event-bus'
  export default {
    components: {
      'v-header': Header,
      'v-container': Container
    },
    data () {
      return {
        data: {}
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.getTemplateInfo()
      })
    },
    methods: {
      getTemplateInfo () {
        const param = {
          bind_id: this.$route.params.bind_id, // 栏目绑定关系
          is_global: this.$route.params.is_global || 0 // 是否是全局模板
        }
        this.$api.getTemplateInfomation(param)
          .then((result) => {
            this.data = result.data[0]
            this.data.is_tpl = 1
            this.data.filename = this.data.sign + '.blade.php'
            this.data.path = this.data.template_file
            this.data.name = this.data.sign + '.blade.php'
            this.data.is_dir = 0
            EventBus.$emit('handleNav', this.data)
          })
      }
    }
  }
</script>

<style lang="scss" scoped>
  section {
    height: 100%;
  }
  .code-container{
    height: calc(100% - 40px);
    padding: 0 20px;
  }
</style>



