import axios from 'axios'
import { baseURL } from '../config/base'
import { Message } from 'element-ui'
import router from '../router'

export default async (url = '', data = {}, method = 'get', config = {}) => {
  method = method.toLowerCase()

  const ajaxConfig = Object.assign({
    url,
    method,
    baseURL,
    withCredentials: true
  }, config)

  if (['post', 'put', 'patch'].includes(ajaxConfig.method)) {
    ajaxConfig.data = data
  } else {
    ajaxConfig.params = data
  }

  try {
    const { errorCode, errorMessage, data } = (await axios(ajaxConfig)).data
    if (errorCode) {
      Message.error(errorMessage)
      throw new Error(errorMessage)
    }
    return data
  } catch (error) {
    if (error.response) {
      const { status } = error.response
      if (status === 401 || status === 403) {
        router.push('/404')
      }
      throw error.response
    } else if (error.request) {
      console.warn(error.request)
      throw error.request
    }
    console.warn('Error', error.message)
    throw error
  }
}



