import Vue from 'vue'
import http from './http'
// template_list: '/template/list',  //
//   assets_list: '/template/assets/list',  //
//   template_detail: '/template/file/detail', //
//   template_update: '/template/file/update', //
//   template_view: '/template/preview'  //
// 模板列表和素材 is_tpl=1/0
export const getTemplate = (data) => http('/template/list', data, 'get')
// 获取模板详情
export const getDetail = (data) => http('/template/detail', data, 'get')
// 获取模板信息
export const getTemplateInfomation = (data) => http('/template/bind', data, 'get')
// 模板更新
export const updateTemplate = (data) => http('/template/detail', data, 'post')
// 模板预览
export const getViewUrl = (data) => http('/template/preview-url', data, 'get')
// 获取套系信息
export const getThemesInfo = (data) => http('/template/themes', data, 'get')
// 文件上传
export const uploadFile = (data) => http('/template/upload', data, 'post')
// 文件删除
export const deleteFile = (data) => http('/template/file/delete', data, 'delete')

Vue.mixin({
  beforeCreate () {
    const options = this.$options
    if (options.api) {
      this.$api = options.api
    } else if (options.parent && options.parent.$api) {
      this.$api = options.parent.$api
    }
  }
})



