import Vue from 'vue'
import Router from 'vue-router'
import DashBoard from '../components/dashboard/dashboard.vue'
import CodeContainer from '../components/codecontainer/codecontainer.vue'
import Error from '../components/404/404.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      redirect: 'app/404'
    },
    {
      path: '/app',
      name: 'app',
      component: {
        template: '<router-view />'
      },
      children: [
        {
          path: '',
          redirect: ''
        },
        {
          path: ':site_id/:client_id/:theme_id',
          component: DashBoard
        },
        {
          path: ':bind_id/:is_global?',
          component: CodeContainer
        },
        {
          path: '404',
          name: '404',
          component: Error
        }
      ]
    }
  ]
})



