<template>
  <section>
    <div class="side-header">
      模板
      <div class="search-box" :class="{'active': toggle}">
        <el-input
        placeholder="输入关键字进行过滤"
        v-model="filterText">
        </el-input>
        <i class="el-icon-search" @click="toggle=!toggle"></i>
      </div>
    </div>

    <el-menu default-active="3" class="el-menu-vertical-demo">
      <el-submenu index="1">
        <template slot="title">模板</template>
        <el-menu-item index="3">
          <el-tree
            class="filter-tree"
            :data="moduleData"
            :props="defaultProps"
            :filter-node-method="filterNode"
            :expand-on-click-node="false"
            accordion
            highlight-current
            @node-click="handleNodeClick"
            :render-content="renderContent"
            ref="module">
          </el-tree>
        </el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">素材</template>
        <el-menu-item index="4">
          <el-tree
            class="filter-tree"
            :data="sourceData"
            :props="defaultProps"
            :filter-node-method="filterNode"
            :expand-on-click-node="false"
            accordion
            highlight-current
            @node-click="handleNodeClick"
            :render-content="renderContent"
            ref="source">
          </el-tree>
        </el-menu-item>
      </el-submenu>
    </el-menu>
  </section>
</template>

<script>
  import { EventBus } from '../../services/event-bus'
  import { MessageBox, Message } from 'element-ui'
  export default {
    data () {
      return {
        toggle: false,
        filterText: '',
        defaultProps: {
          children: 'child',
          label: 'name'
        }
      }
    },
    props: ['moduleData', 'sourceData'],
    watch: {
      filterText (val) {
        this.$refs.module.filter(val)
        this.$refs.source.filter(val)
      }
    },
    methods: {
      renderContent (h, { node, data, store }) {
//        return (
//          <div class="node-item">
//            <span class="node-label">{node.label}</span>
//            <span class="node-control">
//              <i class={{'hidden': data.is_dir === 0, 'el-icon-document': true}}>
//                  <input on-change={(event) => this.handleChange(event, data, store)} type="file" name="file"/>
//              </i>
//              <i class={{'hidden': data.is_dir === 1, 'el-icon-delete2': true}} ref="delete" on-click={ (event) => this.remove(data, event, node) }></i>
//            </span>
//          </div>
//        )
        return (
          <div class="node-item">
            <span class="node-label">{node.label}</span>
            <span class="node-control">

            </span>
          </div>
        )
      },
      filterNode (value, data) {
        if (!value) return true
        return data.name.indexOf(value) !== -1
      },
      remove (data, event, node) {
        event.stopPropagation()
        MessageBox.confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          const param = {
            site_id: this.$route.params.site_id,  // 站点id
            template_sign: this.$route.params.template_name,  // 模板标识
            is_tpl: data.is_tpl,
            filename: data.name,  // 文件名称 N
            directory: data.path // 文件夹 N
          }
          this.$api.deleteFile(param)
            .then(() => {
              Message({
                type: 'success',
                message: '删除成功!'
              })
              if (node.parent.data.child) {
                node.parent.data.child.splice(node.parent.data.child.indexOf(data), 1)
              } else {
                node.parent.data.splice(node.parent.data.indexOf(data), 1)
              }
            })
        }).catch(() => {
          return
        })
      },
      handleChange (ev, data, store) {
        const files = ev.target.files
        if (!files) return
        this.uploadFiles(files, data, store)
      },
      uploadFiles (files, data, store) {
        let formData = new FormData()
        formData.append('site_id', this.$route.params.site_id)
        formData.append('template_sign', this.$route.params.template_name)
        formData.append('is_tpl', data.is_tpl)
        formData.append('directory', data.path)
        for (let i = 0; i < files.length; i++) {
          data.child.push({ name: files[i].name, path: data.path, is_dir: 0 })
          formData.append('file', files[i])
        }
        this.$api.uploadFile(formData)
          .then(() => {
            Message({
              type: 'success',
              message: '添加成功!'
            })
          })
      },
      handleNodeClick (data, node, self) {
        if (data.is_dir === 0) {
          EventBus.$emit('handleNav', data)
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .el-submenu__title{
    padding: 0 10px !important;
  }
  .el-submenu .el-menu-item{
    height: auto;
    line-height: 1;
    padding: 0 !important;
    background-color: transparent;
  }
  .el-submenu{
    &.is-opened{
      background-color: #6F6F6F;
      color:#fff;
    }
  }
  .el-tree{
    background-color: #333;
    border:none;
  }

  .side-header{
    width: calc(100% - 20px);
    height: 44px;
    line-height: 44px;
    color: #989898;
    padding: 0 10px;
    clear: both;
    background-color: #292929;
    .search-box{
      float: right;
      height: 44px;
      display: flex;
      justify-content: flex-end;
      width: calc(100% - 40px);
    }
    .el-icon-search{
      line-height: 44px;
    }
  }
</style>

<style lang="scss">
  .el-submenu__title{
    padding: 0 10px !important;
    height: 36px;
    line-height: 36px;
    background-color: #333;
    color: #999;
    &:hover{
      background-color: #6F6F6F;
      color: #fff;
    }
  }
  .el-submenu{
    &.is-opened{
      .el-submenu__title{
        color:#fff;
        background-color: #6F6F6F;
      }
    }
  }
  .el-tree{
    .el-tree-node__content{
      display: flex;
      justify-content: flex-start;
      align-items: center;
      color:#999;
      &:hover{
        background-color: #6F6F6F;
        color:#fff;
      }
      .node-item{
        flex:1;
        overflow: hidden;
        position: relative;
        .node-label{
          width: calc(100% - 40px);
          display: inline-block;
          padding-right: 40px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .node-control{
          position: absolute;
          right: 20px;
          top: 0;
        }
      }
    }
    .el-tree-node{
      &.is-current{
        .el-tree-node__content{
          background-color: #6F6F6F;
          color:#fff;
        }
      }
    }
  }
  .el-icon-document{
    position: relative;
    width: 14px;
    height: 14px;
    input[type=file] {
      width: 100%;
      height: 100%;
      padding: 0;
      opacity: .000001;
      color: #777;
      position: absolute;
      top: 0;
      left: 0;
      cursor: pointer;
    }
  }
  .hidden{
    display: none;
  }
  .search-box{
    &.active{
      .el-input{
        width: 152px;
        transition: width 0.1s ease 0s;
      }
      .el-input__inner{
        padding: 3px 2px;
      }
    }
    .el-input{
      width: 0;
      .el-input__inner{
        margin-top: 6px;
        padding: 3px 0;
        border: none;
        height: 32px;
        color: #fff;
        background-color: #989898;
      }
      .el-input__inner::-webkit-input-placeholder{
        color:#fff;
      }
    }
  }
</style>



