import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

// 数据结构 默认初始状态
const state = {
  detailItem: {
    id: 0,
    title: ''
  }
}

// 派生出状态 接受 state 作为其第一个参数
const getters = {
  getItem: state => state.detailItem
}

// mutations是唯一允许更新应用状态的地方
const mutations = {
  UPDATEITEM (state, item) {
    state.detailItem = item
  }
}

/**
 * actions定义提交触发更改信息的描述，在actions中可做任意异步操作
 *
 * Action 函数接受一个与 store 实例具有相同方法和属性的 context 对象，
 * 因此你可以调用 context.commit 提交一个 mutation，
 * 或者通过 context.state 和 context.getters 来获取 state 和 getters
 */
const actions = {
  onUpdate (context, item) {
    context.commit('UPDATEITEM', item)
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



