<template>
  <section class="code-wraper">
    <bread-crumb :title="title"></bread-crumb>
    <!--编辑器-->
    <div class="code-container"></div>
    <!--编辑器-->
    <div class="btn-group">
      <el-button :plain="true" type="success" @click="preview()">预览</el-button>
      <el-button type="success" @click="save()">保存</el-button>
    </div>
  </section>
</template>

<script>
  import CodeMirror from 'codemirror'
  import 'codemirror/mode/javascript/javascript.js'
  import 'codemirror/addon/selection/active-line.js' // 区分编辑代码颜色
  import 'codemirror/addon/edit/matchbrackets.js'
  import { EventBus } from '../../services/event-bus'
  import Breadcrumb from './breadcrumb/breadcrumb.vue'
  import { Message } from 'element-ui'
  export default {
    components: {
      'bread-crumb': Breadcrumb
    },
    data () {
      return {
        title: '',
        tempData: {},
        myCodeMirror: Object
      }
    },
    mounted () {
      let targetDom = this.$el.querySelector('.code-container')
      this.myCodeMirror = CodeMirror(targetDom, {
        value: '',
        mode: 'javascript',
        lineWrapping: true,
        lineNumbers: true,
        theme: '3024-night'
      })
      EventBus.$on('handleNav', data => {
        this.tempData = data
        this.getTempDetail(data)
      })
    },
    methods: {
      judgeId: function (id) {
        return (this.$route.params[id] || this.tempData[id])
      },
      usualParam (param) {
        param.site_id = this.judgeId('site_id')  // 站点id
        param.client_id = this.judgeId('client_id') // 终端id
        param.theme_id = this.judgeId('theme_id') // 套系id
      },
      getTempDetail (data) {
        this.tempData.filename = parseInt(this.tempData.is_tpl) === 0 ? this.tempData.name : this.tempData.name.split('.blade.php')[0]
        this.title = this.tempData.filename
        const param = {
          is_tpl: this.tempData.is_tpl,
          filename: this.tempData.filename,  // 文件名称 N
          directory: this.tempData.path // 文件夹 N
        }
        this.usualParam(param)
        this.$api.getDetail(param)
          .then((result) => {
            this.myCodeMirror.getDoc().setValue(result)
          })
      },
      preview () {
        const param = {
          template_sign: this.tempData.sign
        }
        this.$api.getViewUrl(param)
          .then((data) => {
            window.open(data)
          })
      },
      save () {
        let myCode = this.myCodeMirror.getDoc().getValue()
        const param = {
          filename: this.tempData.filename,
          directory: this.tempData.path,
          is_tpl: this.tempData.is_tpl,
          content: myCode
        }
        this.usualParam(param)
        this.$api.updateTemplate(param)
          .then((result) => {
            Message({
              type: 'success',
              message: '保存成功!'
            })
          })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/mixins/scrollbar";
  @import "../../../node_modules/codemirror/lib/codemirror.css";
  @import "../../../node_modules/codemirror/theme/3024-night.css";
  .code-wraper{
    height:100%;
    position: relative;
  }
  .code-container{
    height: calc(100% - 96px);
    position: relative;
    z-index: 2;
    @include scrollbar(10px,#262626,#c2c0c0,#313131);
  }

  .btn-group{
    width: 100%;
    padding: 10px 0;
    height: 36px;
    text-align: right;
    .el-button{
      height: 32px;
      padding: 9px 15px;
    }
    .el-button--success.is-plain {
      background: transparent;
      border: 1px solid #13ce66;
      color: #13ce66;
    }
  }
</style>
<style lang="scss">
  .CodeMirror {
    height: 100%;
  }
  .code-container .CodeMirror-scroll{
    margin-right: 0 !important;
  }

  .code-container .CodeMirror{
    background-color: #313131;
    height: 100%;
    .CodeMirror-gutters{
      background-color: #313131;
    }
  }
</style>




