<template>
  <section>
    <v-header></v-header>
    <div class="content">
      <aside>
        <v-side :module-data="moduleData" :source-data="sourceData"></v-side>
        <!--<v-side :module-data="sourceData"></v-side>-->
      </aside>
      <div class="code-container">
        <v-container></v-container>
      </div>
    </div>
  </section>
</template>

<script>
  import Header from '../header/header.vue'
  import Side from '../aside/aside.vue'
  import Container from '../container/container.vue'
  export default {
    components: {
      'v-header': Header,
      'v-side': Side,
      'v-container': Container
    },
    data () {
      return {
        moduleData: [],
        sourceData: []
      }
    },
    mounted () {
      this.getModules()
      this.getSource()
    },
    methods: {
      getModules () {
        const param = {
          site_id: this.$route.params.site_id,  // 站点id
          client_id: this.$route.params.client_id, // 终端id
          theme_id: this.$route.params.theme_id, // 套系id
          is_tpl: 1,      // 0为素材
          filename: '',  // 文件名称 N
          directory: '' // 文件夹 N
        }
        this.$api.getTemplate(param)
          .then((result) => {
            this.moduleData = result
          })
      },
      getSource () {
        const param = {
          site_id: this.$route.params.site_id,  // 站点id
          client_id: this.$route.params.client_id, // 终端id
          theme_id: this.$route.params.theme_id, // 套系id
          is_tpl: 0, // 0为素材
          filename: '',  // 文件名称 N
          directory: '' // 文件夹 N
        }
        this.$api.getTemplate(param)
          .then((result) => {
            this.sourceData = result
          })
      }
    }
  }
</script>

<style lang="scss" scoped>
  section{
    height: 100%;
  }
  .content{
    display: flex;
    align-self: flex-start;
    height: calc(100% - 40px);
  }
  aside{
    height: 100%;
    background-color: #333333;
    width: 240px;
  }
  .code-container{
    flex:1;
    padding: 0 20px;
  }
</style>



